$enable-shadows:                true !default;
$enable-transitions:            true !default;
$transition-base:               all 150ms linear !default;

$drawerjs-class-prefix:         'drawerjs' !default;
$drawerjs-width:                220px !default;
$drawerjs-bg:                   #251D2A !default;
$drawerjs-color:                $gray-light !default;
$drawerjs-transition:           transform 150ms linear, width 150ms linear, top 150ms linear !default;
$drawerjs-shadow:               2px 0 20px 0 rgba(#000, .3) !default;
// must greater than static navbar and less than fixed navbar
$drawerjs-zindex:               1020 !default;

$drawerjs-holder-bg:            $body-bg !default;
$drawerjs-holder-transition:    transform 150ms linear, margin 150ms linear; // padding 150ms linear !default;

$drawerjs-backdrop-bg:          black !default;
$drawerjs-backdrop-zindex:      990 !default;
